* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  font-size: 14px;
}
body {
  margin: 0;
  color: #fff;
  font-family: serif;
}
section {
  text-align: center;
  padding: 1rem;
}
.airglass-gantt-app {
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    position: relative;
    font-family: "Helvetica Neue", Helvetica, Arial, "PingFang SC", "Hiragino Sans GB", "Heiti SC", "Microsoft YaHei",
      "WenQuanYi Micro Hei", sans-serif;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -moz-user-select: none;
    -webkit-user-drag: none;
  }
  .airglass-fui-gantt-operate {
    position: relative;
    padding: 0 7px;
    height: 100%;
    display: flex;
    align-items: center;

    &:hover {
      & > div {
        display: flex;
      }
    }

    & > div {
      display: none;
      align-items: center;
      position: absolute;
      top: 0px;
      left: 100%;
      height: 100%;
      overflow: hidden;
      box-shadow: 4px 2px 5px 0px hsla(0, 0%, 20%, 0.16);
      border-radius: 0 15px 15px 0;

      & > div {
        display: flex;
        height: 100%;
        align-items: center;
        &:hover {
          background-color: rgba(0, 155, 255, 0.2);
        }
      }
    }
  }
}
